import React, {useState} from 'react'
import axios from 'axios'

import { Button, Modal,  Form, Container, Row, Col    }  from 'react-bootstrap';
import Cookies from 'js-cookie'

import {
    CContainer,
    CRow,
    CButton,
    CCol,
    CFormSelect,
    CFormInput
  } from '@coreui/react'

  import { BsFillPersonLinesFill } from 'react-icons/bs';

const Editar = (props) => {

    const [visible, setVisible] = useState(false)  
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);

    // VARIAVEIS
    const [firstName, setFirstName] = useState(props.data.customer.firstName)
    const [fullName, setFullName] = useState(props.data.customer.fullName)
    const [document, setDocument] = useState(props.data.customer.document)
    const [email, setEmail] = useState(props.data.customer.email)
    const [phone, setPhone] = useState(props.data.customer.phone)

    const userFirstName = (e) => {setFirstName(e.target.value)}
    const userFullName = (e) => {setFullName(e.target.value)}
    const userDocument = (e) => {setDocument(e.target.value)}
    const userEmail = (e) => {setEmail(e.target.value)}
    const userPhone = (e) => {setPhone(e.target.value)}
    const userType = Cookies.get('userType')
    const token = Cookies.get('TokenID') 
    let config = {
      headers: {
        'Authorization': token, 
        'Content-Type': 'application/json;charset=UTF-8',
        "Access-Control-Allow-Origin": "*",
      }
    };
    const urlDomain = 'http://dashboardbff.oonpayperuse.com.br' //'http://localhost:8080'
    const [flag, setFlag] = useState(0)
    function openModal(data){
        const handleShow =  setShow(true);
    }

    function verify(){
        
        // verificacoes no telefone
        setFlag(0)
        if((phone.length == 11) && (phone[2] === '9')){
            salvarEdicoes()
        }else{
            setFlag(1)
        }
    }
    function salvarEdicoes (){

        let upStatus = {firstName:firstName, fullName:fullName, document:document, email:email, phone:phone}
        axios 
            .patch(`${urlDomain}/customers/${props.data.customer.id}`, upStatus, config)
            .then((response) => {
                props.callBack()
                setShow(false)
            })
            .catch(r =>{ 
                console.log('error', r), alert('Login expirado'),window.location.reload()
            })
    }

    return (
        <div> 
            <CButton className='icon-css' size='md' onClick={() => openModal(props)} color="secondary" >
                <BsFillPersonLinesFill />
            </CButton>
            <Modal 
                show={show} 
                onHide={handleClose}
                size="xl"
            >
                    
                <Modal.Header  closeButton>
                    <Modal.Title>Editar informações do cliente </Modal.Title>
                </Modal.Header>
                <Modal.Body className='container-body'>
                    <CRow>
                        <CCol xs={2}>
                        </CCol>

                        <CCol xs={8}>
                            <Form>
                                <Form.Group className="mb-3">
                                    <Form.Label> Primeiro nome </Form.Label>
                                    <Form.Control onChange={userFirstName} value = {firstName} type="text"  />
                                    <br />
                                    <Form.Label> Nome completo</Form.Label>
                                    <Form.Control onChange={userFullName} value = {fullName} type="text"  />
                                    <br />
                                    <Form.Label> Documento</Form.Label>
                                    <Form.Control onChange={userDocument} value = {document} type="text"   />
                                    <br />
                                    <Form.Label> Email  </Form.Label>
                                    <Form.Control onChange={userEmail} value = {email} type="text"  />
                                    <br />
                                    <Form.Label> Phone </Form.Label>
                                    <Form.Control onChange={userPhone} value = {phone} type="text" />
                                    {flag == 1 ? 
                                        <b> Número de tefone errado </b>
                                        :null
                                    }
                                    
                                </Form.Group>
                            </Form> 
                        </CCol>

                        <CCol xs={2}>
                        </CCol>
                       
                    </CRow>
                   
                
                </Modal.Body>
                
                <Modal.Footer>  
                    <Button variant="secondary" onClick={handleClose}>
                        Sair
                    </Button>
                    <Button onClick = {verify} variant="primary" >
                        Salvar Alterações
                    </Button>
                </Modal.Footer>
                    
            </Modal>
            
            

        </div>
    )
}

export default Editar 