import React, { useState, useEffect } from 'react'
import axios  from "axios"
import './css/device.css'
import Cookies from 'js-cookie'

import PlateNumber from './render/PlateNumber'
import IdCli from './render/Id'
import EmailCli from './render/Email'
import ZipCode from './render/ZipCode'
import BtnEditCostumer from './render/BtnEditCostumer'
import Status from './render/Status'
import BtnMidia from './render/Midias/Midia'
import BtnEditAddress from './render/BtnEditAddres'
import NameCli from './render/Name'
import BtnInfos from './render/Infos'
import { BsSearch, BsArrowClockwise, BsChevronDoubleRight , BsChevronDoubleLeft } from 'react-icons/bs';

import { Button, Modal,  Form, Container, Row, Col, Spinner    }  from 'react-bootstrap';
import {
  CButton,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CContainer,
  CTableRow,
  CNavbar,
  CCollapse,
  CNavbarBrand,
  CCard,
  CNavbarNav,
  CNavbarToggler,
  CCardBody,
  CFormSelect,
  CFormInput,
  CForm,
  CPagination,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import {cilLoopCircular, cilMagnifyingGlass, cilChevronRight, cilChevronLeft } from '@coreui/icons'



const Devices = () => {
  const userType = Cookies.get('userType')
  const token = Cookies.get('TokenID') 
  let config = {
    headers: {
      'Authorization': token, 
      'Content-Type': 'application/json;charset=UTF-8',
      "Access-Control-Allow-Origin": "*",
    }
  };

  const [dados, setDados] = useState([])
  const [dadosOri, setDadosOri] = useState([])
  const [busca2, setBusca] = useState('')
  let busca
  var TotalPages
  const [visible, setVisible] = useState(false)  
  const [show, setShow] = useState(false);
  const dados2 =[]
  
  const urlDomain = 'http://dashboardbff.oonpayperuse.com.br'  //'http://localhost:8080'
  const [numberOfPages, setNumberOfPages] = useState()

  useEffect( ()=>{
      axios 
        .get( `${urlDomain}/signatures?pageSize=${qtdPgns}`, config)
        .then((response) => {
            setDados(response.data.content)
            setNumberOfPages(response.data.totalPages)
            initPages(response)
        })
        .catch(r =>{ 
          console.log('error', r), alert('Login expirado'),window.location.reload()
        }) // window.location.reload();
  }, []) 

  function initPages(e){
    if(e.data.totalPages > 1 ){
      TotalPages = e.data.totalPages
      setCurrentPage(1)
      setNextPage(2)
    }
  }

  // pega valores
  const Pegandobusca = (e) =>{
    setBusca(e.target.value)
    busca = e.target.value
    BuscaUser()
  }

  const options = [
    { label: 'Geral ', value: 'true' },
    { label: 'Em análise', value: 'false' }
  ]
  let qtdPgns = 7

  const [flag, setFlag] = useState(false)
  const [array, setArray] = useState([])

  const selectOptonsMenu = (e) =>{
  
    if(e.target.value == 'false'){
      setFlag(true)
      getInAnalyze()
    }else if (e.target.value == 'true'){
      setFlag(false)
      setArray(dadosOri)
    }
  }

  const [previousPage, setPrevios] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [nextPage, setNextPage] = useState('')

  // volta pagina
  function capturePrevious(e, nPage){
    if(currentPage == 1){
      setPrevios('')
      setNextPage(2)
    }else{
      setNextPage(currentPage )
      setCurrentPage(currentPage - 1 )
      setPrevios(currentPage - 2)
    }
    let aux = e -1
    page(aux)
  }

    // Proxima pagina
  function captureNextPage(e, nPage){

    if((currentPage == 1) && (nPage == 2)){
      setCurrentPage(currentPage + 1)
      setPrevios(1)
      setNextPage('')
    }else if((currentPage == 1) && (nPage != 2)){
      setNextPage(3)
      setCurrentPage(currentPage + 1)
      setPrevios(1)
    }else if(currentPage < nPage -1){ 
      setNextPage(currentPage + 2)
      setCurrentPage(currentPage + 1)
      setPrevios(currentPage )
    }else{
      setNextPage('')
      setCurrentPage(currentPage + 1)
      setPrevios(currentPage)
    }
    let aux = e -1
    page(aux)
  } 

  function page(e){

    setDados([])
     axios 
      .get(`${urlDomain}/signatures?pageSize=${qtdPgns}&pageNumber=${e}`, config)
      .then((response) => {
        setDados(response.data.content)
        setNumberOfPages(response.data.totalPages)
        
      })
      .catch(r =>{ 
        console.log('error', r), alert('Login expirado'),window.location.reload()
      })
  }

  function refreshPage (){
    setDados([])
    axios 
     .get(`${urlDomain}/signatures?pageSize=${qtdPgns}`, config)
     .then((response) => {
       setDados(response.data.content)
       setNumberOfPages(response.data.totalPages)
       initPages(response)
       //
     })
     .catch(r =>{ 
       console.log('error', r), alert('Login expirado'),window.location.reload()
     })
  }

  if(dados == ''){
    return (<> <Spinner animation="grow" variant="info" /> </>)
  }else{
   
      return (
        <>
          <CCard className='deviceContainer'>
            <CCardBody>
              <CNavbar expand="lg"  className="menuDeviceContainer">
                <CContainer fluid>
                  <CNavbarToggler
                    aria-label="Toggle navigation"
                    aria-expanded={visible}
                    onClick={() => setVisible(!visible)}
                  />
                  <CCollapse className="navbar-collapse" visible={visible}>
                    <CNavbarBrand> Dispositivos  </CNavbarBrand>
                    <CNavbarBrand> 
                      <CFormSelect 
                        aria-label="Default select example"
                        options={options}
                        onChange={selectOptonsMenu}
                        disabled
                      />
                    </CNavbarBrand>
                    
                    <CNavbarNav className="me-auto mb-2 mb-lg-0">
                    </CNavbarNav>
                    <CForm className="d-flex">
                      <CFormInput disabled type="search" onChange={Pegandobusca} value={busca2} color="dark" className="me-2" placeholder="Pesquise " />
                      <CButton type="submit"  color="secondary"  variant='ghost'  >
                        <CIcon icon={cilLoopCircular} />
                      </CButton>
                    </CForm>
                  </CCollapse>
                </CContainer>
              </CNavbar>

              <CTable striped  >
                <CTableHead >
                  <CTableRow>
                    <CTableHeaderCell > Placa Veículo</CTableHeaderCell>
                    <CTableHeaderCell > Nome</CTableHeaderCell>
                    <CTableHeaderCell > E-mail</CTableHeaderCell>
                    <CTableHeaderCell > Cidade</CTableHeaderCell>
                    <CTableHeaderCell > Status</CTableHeaderCell>
                    <CTableHeaderCell > Ações</CTableHeaderCell>
                    {/* <CTableHeaderCell> Imagens</CTableHeaderCell> */}
                  </CTableRow>
                </CTableHead>

                <CTableBody>
                  {/* Percorre os usuarios  */}
                  {dados.map((item, index) => (
                    <CTableRow key={index}>

                      <CTableDataCell >
                        <PlateNumber plate={item.vehicle.plateNumber} />
                      </CTableDataCell>
                      
                      <CTableDataCell >
                        <NameCli name={item.customer.firstName} />
                      </CTableDataCell>

                      <CTableDataCell >
                        <EmailCli email={item.customer.email} />
                      </CTableDataCell>

                      <CTableDataCell >
                        <ZipCode zipCode={item.vehicle.city} />
                      </CTableDataCell>

                      <CTableDataCell >
                        <Status status= {item} callBack={refreshPage} />
                      </CTableDataCell>

                      <CTableDataCell  >
                        <div className='container-device-actions'> 
                          <BtnEditCostumer  data={item} callBack={refreshPage} />
                          <BtnEditAddress address={item} callBack={refreshPage} />
                          <BtnMidia midia={item.vehicle.id} />
                          <BtnInfos infos={item}  />
                        </div>
                      </CTableDataCell> 

                    </CTableRow>
                    
                  ))}

                </CTableBody>
              </CTable>


              <CPagination className='container-devices-pagination' color='dark'  aria-label="Page navigation example">
                {/* <label className='container-qtd-employeer'> Há {n_employeer} empregados</label> */}
                <div className='container-pagination-align'>
                  
                  {previousPage != '' ?
                    (
                      <> 
                        <CButton className='btn-user-pagination' color='dark' onClick={ () =>{capturePrevious(previousPage, numberOfPages)}} variant='outline'> <BsChevronDoubleLeft/> </CButton>
                        <CButton className='btn-user-pagination' color='dark' onClick={ () => {capturePrevious(previousPage, numberOfPages)}} variant='outline'> {previousPage} </CButton>
                      </>
                      
                    )
                    :null
                  }

                  {currentPage == 0 ?
                    (<CButton className='btn-user-pagination' defaultChecked color='dark' active={true}  > {currentPage+1}</CButton>)
                    :
                    (<CButton className='btn-user-pagination' defaultChecked color='dark' active={true}  > {currentPage}</CButton>)
                
                  }
                  
                  {nextPage != '' ? 
                    (
                      <>
                        <CButton className='btn-user-pagination' color='dark' onClick={() => captureNextPage(nextPage,numberOfPages)} variant='outline' >  {nextPage} </CButton>
                        <CButton  className='btn-user-pagination' color='dark' onClick={() => captureNextPage(nextPage, numberOfPages)} variant='outline' > <BsChevronDoubleRight /> </CButton>
                      </>
                    )
                    :null
                  }
                </div>
                
              </CPagination>
            </CCardBody>
          </CCard>
        </>
      )
    }
}
export default Devices
