import React, {useState} from 'react'
import './status.css'
import Cookies from 'js-cookie'
import {
    CButton,
    CFormSelect
  
  } from '@coreui/react'

import axios from 'axios'

import { Modal  }  from 'react-bootstrap';
import { BsArrowDownUp  } from 'react-icons/bs';


const Status= (propsStatus) => {

    const [visible, setVisible] = useState(false)  
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const openModal = () => setShow(true);

    const userType = Cookies.get('userType')
    const token = Cookies.get('TokenID') 
    let config = {
      headers: {
        'Authorization': token, 
        'Content-Type': 'application/json;charset=UTF-8',
        "Access-Control-Allow-Origin": "*",
      }
    };

    const [choice, setChoice] = useState(propsStatus.status.status)

    const options = [
        { label: 'Aguardando pagamento', value: 'WAITING_PAYMENT' },
        { label: 'Em análise', value: 'IN_ANALYSE' },
        { label: 'Bloqueado', value: 'BLOCKED'},
        { label: 'Cancelado', value: 'CANCELLED'},
        { label: 'Enviado', value: 'SENT'},
        { label: 'Reprovado', value: 'REPROVED'},
        { label: 'Sicronizado', value: 'SYNCHRONIZED'},
        { label: 'Aprovado', value: 'APPROVED'},
        { label: 'Falha no pagamento', value: 'PAYMENT_FAILURE'},
    ]


    const updateStatus = (e) => {
        setChoice(e.target.value)
    }


    const salvarEdicoes = () =>{
        let upStatus = {status:choice}
  
        const urlDomain = 'http://dashboardbff.oonpayperuse.com.br' //'http://localhost:8080'

        axios 
            .patch(`${urlDomain}/signatures/${propsStatus.status.id}`, upStatus, config)
            .then((response) => {
                propsStatus.callBack()
                setShow(false)
            })
            .catch(r =>{ 
                console.log('error', r), alert('Login expirado'),window.location.reload()
            })
    }


    return(
        (
            <>
                {propsStatus.status.status == 'WAITING_PAYMENT' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Aguardando pagamento.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                             </CButton> 
                        </div>
                    )
                    :null
                }

                {propsStatus.status.status == 'IN_ANALYSE' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto' className='texto'>Em análise.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                             </CButton> 
                        </div>
                    )
                    :null
                }

                {propsStatus.status.status == 'APPROVED' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Pagamento aprovado.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                             </CButton> 
                        </div>
                    )
                    :null
                }

                {propsStatus.status.status == 'BLOCKED' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Bloqueado.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                             </CButton> 
                        </div>
                    )
                    :null
                }

                 {propsStatus.status.status == 'SUSPENDED' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Suspenso.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                            </CButton> 
                        </div>
                    )
                    :null
                }
                {propsStatus.status.status == 'DISABLED' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Disabilitado.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                             </CButton>
                        </div>
                    )
                    :null
                }

                {propsStatus.status.status == 'SENT' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Enviado.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                            </CButton> 
                        </div>
                    )
                    :null
                }

                {propsStatus.status.status == 'SYNCHRONIZED' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Sicronizado.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                            </CButton> 
                        </div>
                    )
                    :null
                }

                {propsStatus.status.status == 'CANCELLED' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Cancelado.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                            </CButton> 
                        </div>
                    )
                    :null
                }

                {propsStatus.status.status == 'REPROVED' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Reprovado.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                            </CButton> 
                        </div>
                    )
                    :null
                }

                {propsStatus.status.status == 'PAYMENT_FAILURE' ? 
                    (
                        <div className='container-status'> 
                            <label className='texto'>Pagamento reprovado.</label>
                            <CButton variant = 'ghost' className='button-status-select' size='sm' onClick={openModal} color="dark" > 
                                <b><BsArrowDownUp className='icon-format' /></b>
                            </CButton> 
                        </div>
                    )
                    :null
                }
                
                <Modal 
                    show={show} 
                    onHide={handleClose}
                    size="sm"
                >
                    
                <Modal.Body className='container-body'>
                    <h4>Alterar status </h4>
                    <br />
                    <label> {propsStatus.email}</label>
                    <br />
                    <br />
                    <CFormSelect 
                        aria-label="Default select example"
                        options={options}
                        value={choice}
                        onChange={updateStatus}
                    />
                    <br />
                    <br />
                    
                    <CButton color="dark" onClick={salvarEdicoes} size='sm' >
                        Salvar Alterações
                    </CButton>

                </Modal.Body>
                
            </Modal>
            </>
        )
    )
 
    
}

export default Status
