import React, {useState} from 'react'
import { Button, Modal,  Form, Container, Row, Col    }  from 'react-bootstrap';
import axios from 'axios'
import Cookies from 'js-cookie'

import {
    CContainer,
    CRow,
    CButton,
    CCol,
    CFormSelect,
    CFormInput
  } from '@coreui/react'

import { BsFillSignpostSplitFill } from 'react-icons/bs';
import { useEffect } from 'react';

const BtnEditAddress = (props) =>{

    const [visible, setVisible] = useState(false)  
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);

    // variaveis
    const [addressId, setAddressId] = useState()
    const [addressData, setAddressData] = useState({address:'',complement:'', zipCode:'', state:'',city:'' , district:'', number:'' })

    const [address, setAddress] = useState()
    const [complement, setComplement] = useState()
    const [district, setDisctrict ] = useState()
    const [zipCode, setZipCode] = useState()
    const [state, setState] = useState()
    const [city, setCity] = useState()
    const [number, setNumber] = useState()

    const userAddress = (e) => {setAddress(e.target.value)}
    const userComplement = (e) => {setComplement(e.target.value)}
    const userDistrict = (e) =>{setDisctrict(e.target.value)}
    const userZipCode = (e) =>{setZipCode(e.target.value)}
    const userState = (e) => {setState(e.target.value)}
    const userCity = (e) => {setCity(e.target.value)}
    const userNumber = (e) =>{setNumber(e.target.value)}

    const token = Cookies.get('TokenID')
    let config = {
        headers: {
          'Authorization': token, 
          'Content-Type': 'application/json;charset=UTF-8',
          "Access-Control-Allow-Origin": "*",
        }
      };

      const urlDomain = 'http://dashboardbff.oonpayperuse.com.br' //'http://localhost:8080'

    function openModal(data){
       
        axios 
            .get(`${urlDomain}/customers/${props.address.customer.id}`, config)
            .then((response) => {
                setAddressData(response.data.addresses[0])
                const handleShow =  setShow(true);
            })
            .catch(r =>{ 
                console.log('error', r), alert('Login expirado'),window.location.reload()
            })
    }
    
    useEffect(()=>{
        setAddress(addressData.address)
        setComplement(addressData.complement)
        setAddressId(addressData.id)
        setZipCode(addressData.zipCode)
        setNumber(addressData.number)
        setState(addressData.state)
        setCity(addressData.city)
        setDisctrict(addressData.district)
    },[addressData.address])



    function salvarEdicoes (){

        let upStatus = {address: address ,complement:complement, district:district, zipCode:zipCode, state:state, city:city, number:number  }

        axios 
            .patch(`${urlDomain}/addresses/${addressId}`, upStatus, config)
            .then((response) => {
                props.callBack()
                console.log(response)
                setShow(false)
            })
            .catch(r =>{ 
                console.log('error', r), alert('Login expirado'),window.location.reload()
            })
        
    }

   
        return (
            <> 
                <CButton className='icon-css' size='md' onClick ={openModal} color="secondary" >
                    <BsFillSignpostSplitFill />
                </CButton>
    
                <Modal 
                    show={show} 
                    onHide={handleClose}
                    size="xl"
                >
                        
                    <Modal.Header  closeButton>
                        <Modal.Title> Editar Endereço do cliente </Modal.Title>
                    </Modal.Header>
    
                    <Modal.Body className='container-body'>
                    
                        <CRow>
                            <CCol xs={2}>
    
                            </CCol>
    
                            <CCol xs={8}>
                                <Form>
                                    <Form.Group className="mb-3">
                                        <Form.Label> Endereço </Form.Label>
                                        <Form.Control onChange={userAddress} value = {address} type="text"  />
                                        <br />
                                        <Form.Label> Número </Form.Label>
                                        <Form.Control onChange={userNumber} value = {number} type="text"  />
                                        <br />
                                        <Form.Label> Complemento</Form.Label>
                                        <Form.Control onChange={userComplement} value = {complement} type="text"  />
                                        <br />
                                        <Form.Label> Bairro</Form.Label>
                                        <Form.Control onChange={userDistrict} value = {district} type="text"   />
                                        <br />
                                        <Form.Label> Cep  </Form.Label>
                                        <Form.Control onChange={userZipCode} value = {zipCode} type="text"  />
                                        <br />
                                        <Form.Label> Estado </Form.Label>
                                        <Form.Control onChange={userState} value = {state} type="text" />
                                        <br />
                                        <Form.Label> Cidade </Form.Label>
                                        <Form.Control onChange={userCity} value = {city} type="text" />
                                    </Form.Group>
                                </Form> 
                            </CCol>
    
                            <CCol xs={2}>
                                
                            </CCol>
                           
                        </CRow>
                    </Modal.Body>
                    
                    <Modal.Footer>  
                        <CButton color="secondary" onClick={handleClose}>
                            Sair
                        </CButton>
                        <CButton  onClick ={salvarEdicoes} color="dark" >
                            Salvar Alterações
                        </CButton>
                    </Modal.Footer>
                        
                </Modal>
            </>
        )
    

    
}


export default BtnEditAddress